import React from 'react';
import './Form.css';

const SocialLogin = () => {
    return (
        <div className="social-login">
            <p>Sign in With</p>
        <button className="btn logo">
          <img src="google-logo.png" alt="Google-logo" /> Google
        </button>
        <button className="btn logo">
          <img src="github-logo.png" alt="GitHub-logo" /> GitHub
        </button>
        </div>
    )
}

export default SocialLogin
